<template>
  <div class="flex flex-1 min-h-screen flex-col bg-primary loginApi">
    <section class="flex flex-1 justify-center items-center flex-col justify-center">
      <div class="py-16" @click="toHome">
        <img src="@assets/images/logo.png" alt="" class="logo">
      </div>
      <div class="flex flex-col items-center justify-center">
        <div class="sm:rounded-sm bg-white shadow p-6 w-105 shadow-lg"><h1
                class="text-center mb-6 font-normal font-body text-2xl">{{$t('resetPassword')}}</h1>
          <div class="mb-6">
            <div>
              <div class="flex flex-wrap justify-between"><label class="pr-2 mb-2">{{$t('password')}}<span
                      class="text-pink">*</span></label></div>
              <password  @changecolor="changeIcon"></password>
              <div class="flex flex-wrap justify-between"><label class="pr-2 mb-2">{{$t('confirm')}}<span
                      class="text-pink">*</span></label></div>
              <input type="password"
                     class="h-10 flex-1 block w-full border border-gray-400 placeholder-gray-400 rounded-sm mb-6"
                     name="password_verify" v-model="password_verify"/>
              <button class="inline-flex items-center justify-center font-mono uppercase transition ease-in duration-200 hover:no-underline active:shadow-inner focus:outline-none rounded-sm h-10 px-4 text-sm border text-white shadow hover:shadow-hover hover:border-transparent active:border-transparent bg-black border-black hover:text-pink w-full"
                      @click="resetPassword">{{$t('submit')}}
              </button>
            </div>
            <div></div>
          </div>
        </div>
        <div class="text-center p-6">
          <p class="text-white text-sm">{{$t('cancelReset')}}, <a
                class="text-white underline hover:text-black" @click="toLogin">{{$t('login')}}</a></p></div>
      </div>
    </section>
    <footpage></footpage>
  </div>


</template>
<script>
import api from '@/api';
import mixin from '@mixin';
import layer from '@/utils/layer';
import validate from '@/utils/validate';
import Password from '@/components/settings/password.vue';
import Footpage from '@components/footpage';

export default {
  name: 'resetPassword',
  mixins: [mixin],
  components: {
    Password,
    Footpage,
  },
  data() {
    return {
      new_password: null,
      password_verify: null,
      errorType: [],
    };
  },
  methods: {
    changeIcon(arr) {
      this.$set(this, 'errorType', arr.type);
      this.password = arr.new_password;
    },
    resetPassword() {
      const { password, password_verify } = this;
      const obj = {
        password,
        password_verify,
        token: this.$route.params.token,
      };
      const errorType = this.errorType.filter(i => i > 0);

      if (errorType.length < 4) {
        layer.tip(this.$t('correct'), 'error');
        return;
      }

      this.validResult = validate.isRequiredCheck(obj);

      if (!this.validResult.valid) {
        layer.tip(this.validResult.msg, 'error');
      } else {
        api.loginApi.resetnewpassword(obj).then((result) => {
          if (!result.success) {
            // layer.tip(this.$t('registerError'), 'error');
            layer.tip(result.error, 'error');
          } else {
            layer.tip(this.$t('resetsuccess'));
            setTimeout(() => {
              this.$router.push('/loginRegister/login');
            }, 1000);
          }
        });
      }
    },
  },

};
</script>
<style scoped lang="less">
  .loginApi{
    .logo{
      height: 40px;
      cursor: pointer;
    }
  }
</style>
