import TipLayer from '@components/TipLayer';
import Vue from 'vue';

const TipLayerClass = Vue.extend(TipLayer);

export default {
  tip(msg, type, callback) {
    const tipLayer = new TipLayerClass();
    tipLayer.msg = msg;
    tipLayer.type = type;
    const divElement = document.createElement('div');
    divElement.setAttribute('id', 'layerBrdr');
    document.body.appendChild(divElement);
    tipLayer.$mount('#layerBrdr');

    setTimeout(() => {
      const thisNode = document.getElementById('layer');
      tipLayer.$destroy();
      thisNode.parentNode.removeChild(thisNode);

      return callback ? callback() : null;
    }, 3000);
  },
};
