const isRequiredCheck = (data, type) => {
  let validObj = { valid: true, msg: '' };
  for (const item in data) {
    if (data.hasOwnProperty(item)) {
      const val = data[item];
      if (val === '' || val === undefined || !val || val.length === 0 || val === null) {
        const msg = type === 'zh' ? `${item}不可为空` : `${item} is required`;
        validObj = { valid: false, msg };
        return validObj;
      }
      if (item === 'email') {
        const reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
        if (!reg.test(data[item])) {
          const msg = type === 'zh' ? '邮箱地址错误' : 'email address is wrong';
          return { valid: false, msg };
        }
      }
      const msgVerify = type === 'zh' ? '请确保再次输入的密码相同' : 'Please keep the same password as your password for confirmation';
      if (data.hasOwnProperty('new_password')) {
        if (data.password_verify !== data.new_password) {
          return { valid: false, msg: msgVerify };
        }
      } else if (item === 'password_verify') {
        if (data.password_verify !== data.password) {
          return { valid: false, msg: msgVerify };
        }
      }
    }
  }
  return validObj;
};

export default {
  isRequiredCheck,
};
